import React from "react";
import VideoContainer from "../VideoContainer";
import Video from "../Video";
import { getDefaultPreset, getVideoFile, VideoData } from "../../data";
import VideoPicker from "../VideoPicker";

interface VideoExplorerProps {}

interface VideoExplorerState {
  videos: VideoData[];
  videoLengths: { [videoUrl: string]: number };
  videoPosition: number;
  lastLoopStarted: number;
  updateInterval?: number;
}

export default class VideoExplorer extends React.Component<
  VideoExplorerProps,
  VideoExplorerState
> {
  constructor(props: VideoExplorerProps) {
    super(props);
    this.state = {
      videos: getDefaultPreset(),
      videoLengths: {},
      videoPosition: 0,
      lastLoopStarted: window.performance.now()
    };
    this.onAddVideo = this.onAddVideo.bind(this);
    this.onLoadPreset = this.onLoadPreset.bind(this);
    this.onRestartVideos = this.onRestartVideos.bind(this);
    this.tick = this.tick.bind(this);
  }

  public render() {
    return (
      <>
        <div className="container">
          <div className="row">
            <div className="col offset-sm-2">
              <h2 id="videos">Explore Videos</h2>
            </div>
            <div className="col col-auto">
              <VideoPicker
                onAddVideo={this.onAddVideo}
                onLoadPreset={this.onLoadPreset}
                alreadyPickedVideos={this.state.videos}
                onRestartVideos={this.onRestartVideos}
              />
            </div>
          </div>
          <div className="row">
            <div className="col offset-sm-2">
              <p>Use "Load Preset" to compare videos from the same environment.
                 Use the "Add" button to explore beyond the presets.
                 By default, the videos are synchronized.
                 Click the lock icon to unlock the playback controls.
              </p>
            </div>
          </div>
        </div>
        <VideoContainer>
          {this.state.videos.map(video => (
            video === "linebreak"
            ? <br />
            : <Video
              key={JSON.stringify(video)}
              name={`${video[0]}: ${video[1]} vs ${video[2]}`}
              videoFile={getVideoFile(video)}
              position={this.state.videoPosition / 1000}
              onVideoDurationKnown={this.onVideoDurationKnown.bind(
                this,
                getVideoFile(video)
              )}
              onVideoRemove={this.onRemoveVideo.bind(this, video)}
            />
          ))}
        </VideoContainer>
      </>
    );
  }

  public componentDidMount(): void {
    setInterval(this.tick, 80);
  }

  public componentWillUnmount() {
    clearInterval(this.state.updateInterval);
  }

  private onLoadPreset(videos: VideoData[]) {
    const videoFiles = videos.map(video => (
      video === "linebreak"
      ? ""
      : getVideoFile(video))
    );
    const videoLengths: { [videoUrl: string]: number } = {};

    Object.entries(this.state.videoLengths).forEach(([videoFile, length]) => {
      if (videoFiles.indexOf(videoFile) > -1) {
        videoLengths[videoFile] = this.state.videoLengths[videoFile];
      }
    });
    this.setState({
      videos,
      videoLengths: videoLengths,
      videoPosition: 0,
      lastLoopStarted: window.performance.now(),
    });
  }

  private onAddVideo(video: VideoData) {
    const videos = this.state.videos.slice();
    videos.push(video);

    this.setState({
      videos,
    });
  }

  private onRemoveVideo(videoToRemove: VideoData) {
    const { videos, videoLengths } = this.state;
    const videoFileToRemove = getVideoFile(videoToRemove);
    delete videoLengths[videoFileToRemove];
    this.setState({
      videos: videos.filter(video => JSON.stringify(video) !== JSON.stringify(videoToRemove)),
      videoLengths,
    });
  }

  private onVideoDurationKnown(videoUrl: string, duration: number) {
    this.setState({
      videoLengths: {
        ...this.state.videoLengths,
        [videoUrl]: duration * 1000
      }
    });
  }

  private onRestartVideos() {
    if (Object.values(this.state.videoLengths).length > 0) {
      this.setState({
        videoPosition: 0,
        lastLoopStarted: window.performance.now(),
      });
    }
  }

  private getMaxVideoLength() {
    if (Object.values(this.state.videoLengths).length === 0) {
      return -1;
    }

    return Math.max(...Object.values(this.state.videoLengths));
  }

  private tick() {
    const now = window.performance.now();

    const currentLoopTime = now - this.state.lastLoopStarted;
    if (currentLoopTime > this.getMaxVideoLength()) {
      this.setState({
        videoPosition: 0,
        lastLoopStarted: now,
      });
    } else {
      this.setState({
        videoPosition: currentLoopTime,
      });
    }
  }
}
