import React from "react";
import ReactModal from "react-modal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {fileTree, presets, VideoData} from "../../data";

import "./index.scss";

interface VideoPickerProps {
  onLoadPreset: (videos: VideoData[]) => void;
  onAddVideo: (videoUrl: VideoData) => void;
  onRestartVideos: () => void;
  alreadyPickedVideos: VideoData[];
}

interface VideoPickerState {
  pickerOpen: boolean;
  selectedEnvironment: string | null;
  selectedAdversary: string | null;
}

export default class VideoPicker extends React.Component<
  VideoPickerProps,
  VideoPickerState
> {
  constructor(props: VideoPickerProps) {
    super(props);
    this.openVideoPicker = this.openVideoPicker.bind(this);
    this.onPickerCancel = this.onPickerCancel.bind(this);
    this.onLoadPreset = this.onLoadPreset.bind(this);
    this.onRestartVideos = this.onRestartVideos.bind(this);
    this.state = {
      pickerOpen: false,
      selectedEnvironment: null,
      selectedAdversary: null
    };
  }

  public render() {
    return (
      <>
        <div
          className="btn-group"
          role="group"
          aria-label="Comparison controls"
        >
          {this.state.pickerOpen ||
            <button
              className="btn btn-primary"
              onClick={this.openVideoPicker}
            >
              <FontAwesomeIcon icon="plus" /> Add
            </button>
          }

          <div className="btn-group">
            <button
              className="btn btn-primary dropdown-toggle"
              id="load-preset-button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              Load Preset
            </button>
            <div
              className="dropdown-menu"
              aria-labelledby="load-preset-button"
            >
              {presets.map(({ name, videos }) => (
                <button
                  key={name}
                  className="dropdown-item"
                  onClick={this.onLoadPreset.bind(this, videos)}
                >
                  {name}
                </button>
              ))}
            </div>
          </div>

          <button className="btn btn-secondary" onClick={this.onRestartVideos}>
            <FontAwesomeIcon icon="fast-backward" /> Restart
          </button>

          <button className="btn btn-danger" onClick={this.onLoadPreset.bind(this, [])}>
            <FontAwesomeIcon icon="ban" /> Clear
          </button>
        </div>

        <ReactModal
          isOpen={this.state.pickerOpen}
          contentLabel="Select video to add to comparison"
          shouldCloseOnOverlayClick={false}
        >
          <button
            className="btn btn-secondary VideoPicker__close"
            onClick={this.onPickerCancel}
            aria-label="Close picker"
          >
            <FontAwesomeIcon icon="window-close" />
          </button>

          <div className="container-fluid">
            <div className="row">
              <div className="col-4">
                <h2 className="text-center">Environment</h2>
              </div>
              <div className="col-4">
                <h2 className="text-center">Opponent</h2>
              </div>
              <div className="col-4">
                <h2 className="text-center">Victim</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-4">
                <ul className="list-group">
                  {Object.keys(fileTree).map(environment => (
                    <li
                      key={environment}
                      className={`list-group-item ${
                        environment === this.state.selectedEnvironment
                          ? "active"
                          : ""
                        }`}
                    >
                      <button
                        className="btn btn-block"
                        onClick={this.onSelectEnvironment.bind(this, environment)}
                      >
                        {environment}
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
              {this.state.selectedEnvironment !== null && (
                <>
                  <div className="col-4">
                    <ul className="list-group">
                      {Object.keys(fileTree[this.state.selectedEnvironment]).map(
                        adversary => (
                          <li
                            key={adversary}
                            className={`list-group-item ${
                              adversary === this.state.selectedAdversary
                                ? "active"
                                : ""
                              }`}
                          >
                            <button
                              className="btn btn-block"
                              onClick={this.onSelectAdversary.bind(
                                this,
                                adversary
                              )}
                            >
                              {adversary}
                            </button>
                          </li>
                        )
                      )}
                    </ul>
                  </div>
                  {this.state.selectedAdversary !== null && (
                    <div className="col-4">
                      <ul className="list-group">
                        {Object.keys(
                          fileTree[this.state.selectedEnvironment][
                            this.state.selectedAdversary
                            ]
                        ).map(victim => (
                          <li key={victim} className="list-group-item">
                            <button
                              className="btn btn-block"
                              onClick={this.onPick.bind(this, [
                                this.state.selectedEnvironment as string,
                                this.state.selectedAdversary as string,
                                victim
                              ])}
                              disabled={videoIn(
                                [
                                  this.state.selectedEnvironment as string,
                                  this.state.selectedAdversary as string,
                                  victim
                                ],
                                this.props.alreadyPickedVideos
                              )}
                            >
                              {victim}
                            </button>
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                </>
              )}
            </div>
          </div>
        </ReactModal>
      </>
    );
  }

  private openVideoPicker() {
    this.setState({ pickerOpen: true });
  }

  private onPickerCancel() {
    this.setState({ pickerOpen: false });
  }

  private onPick(video: VideoData) {
    this.setState({ pickerOpen: false });
    this.props.onAddVideo(video);
  }

  private onLoadPreset(videos: VideoData[]) {
    this.props.onLoadPreset(videos);
  }

  private onRestartVideos() {
    this.props.onRestartVideos();
  }

  private onSelectEnvironment(enviroment: string) {
    this.setState({
      selectedEnvironment: enviroment,
      selectedAdversary: null
    });
  }

  private onSelectAdversary(adversary: string) {
    this.setState({
      selectedAdversary: adversary
    });
  }
}

function videoIn(needle: VideoData, videos: VideoData[]) {
  const comparableVideos = videos.map(video => JSON.stringify(video));
  return comparableVideos.indexOf(JSON.stringify(needle)) > -1;
}
