import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import "./index.scss";

const VIDEO_BASE_URL =
  "https://adversarial-policies-public.s3.amazonaws.com/videos/";
const VIDEO_PREVIEW_SUFFIX = "_720p.mp4";
const VIDEO_FULL_SUFFIX = "_1080p.mp4";

interface VideoProps {
  name: string;
  videoFile: string;
  position: number;
  onVideoDurationKnown: (duration: number) => void;
  onVideoRemove: () => void;
}

interface VideoState {
  synchronized: boolean;
}

export default class Video extends React.Component<VideoProps, VideoState> {
  private videoElem: HTMLVideoElement | null = null;

  constructor(props: VideoProps) {
    super(props);
    this.state = { synchronized: true };
    this.getVideoElemRef = this.getVideoElemRef.bind(this);
    this.videoDataLoaded = this.videoDataLoaded.bind(this);
    this.onVideoRemove = this.onVideoRemove.bind(this);
    this.onLockToSynchronization = this.onLockToSynchronization.bind(this);
    this.onUnlockFromSynchronization = this.onUnlockFromSynchronization.bind(this);
  }

  public render() {
    return (
      <div className="Video">
        <h3 className="h5 Video__title">
          <span className="Video__title-content">{this.props.name}</span>
          <div className="btn-group float-right">
            {
              this.state.synchronized
              ?
                <button
                  className="btn btn-outline-primary"
                  aria-label="Enable video controls"
                  title="Enable video controls"
                  onClick={this.onUnlockFromSynchronization}
                >
                  <FontAwesomeIcon icon="lock" />
                </button>
              :
                <button
                  className="btn btn-outline-primary"
                  aria-label="Synchronize video to global playback"
                  title="Synchronize video to global playback"
                  onClick={this.onLockToSynchronization}
                >
                  <FontAwesomeIcon icon="unlock" />
                </button>
            }
            <a
              href={`${VIDEO_BASE_URL}${
                this.props.videoFile
              }${VIDEO_FULL_SUFFIX}`}
              target="_blank"
              rel="noopener noreferrer"
              className="btn btn-outline-secondary"
              title="Open video in new tab"
              aria-label="Open video in new tab"
            >
              <FontAwesomeIcon icon="external-link-alt" />
            </a>
            <button
              className="btn btn-outline-danger"
              title="Remove video"
              aria-label="Remove video"
              onClick={this.onVideoRemove}
            >
              <FontAwesomeIcon icon="window-close" />
            </button>
          </div>
        </h3>

        <div className="embed-responsive embed-responsive-16by9">
          <video
            src={`${VIDEO_BASE_URL}${this.props.videoFile}${VIDEO_PREVIEW_SUFFIX}`}
            ref={this.getVideoElemRef}
            muted={true}
            controls={!this.state.synchronized}
          />
        </div>
      </div>
    );
  }

  public componentDidUpdate() {
    this.updateElemState();
  }

  public componentWillUnmount() {
    if (this.videoElem) {
      this.videoElem.removeEventListener("loadeddata", this.videoDataLoaded);
    }
  }

  private getVideoElemRef(videoElem: HTMLVideoElement | null) {
    this.videoElem = videoElem;
    if (videoElem) {
      videoElem.addEventListener("loadeddata", this.videoDataLoaded);
      this.updateElemState();
    }
  }

  private videoDataLoaded() {
    if (this.videoElem) {
      this.props.onVideoDurationKnown(this.videoElem.duration);
    }
  }

  private onLockToSynchronization() {
    this.setState({ synchronized: true });
  }

  private onUnlockFromSynchronization() {
    this.setState({ synchronized: false });
  }

  private onVideoRemove() {
    this.props.onVideoRemove();
  }

  private updateElemState() {
    if (!this.videoElem || this.videoElem.readyState < 2) {
      return;
    }

    if (this.state.synchronized) {
      const skew = Math.abs(this.videoElem.currentTime - this.props.position);
      if (this.props.position > this.videoElem.duration) {
        this.videoElem.currentTime = this.videoElem.duration;
      } else if (skew > 0.2) {
        this.videoElem.currentTime = this.props.position;
      }

      if (this.videoElem.paused) {
        this.videoElem.play();
      }
    }
  }
}
