import _presets from "./presets.json";
import _fileTree from "./videos.json";

export type VideoData = [string, string, string] | "linebreak";

export const presets = _presets as {
  "name": string,
  "videos": VideoData[]
}[];

export const fileTree = _fileTree as {
  [environment: string]: {
    [adversary: string]: {
      [victim: string]: string;
    };
  };
};

export function getDefaultPreset(): VideoData[] {
  return presets[0].videos;
}

export function getVideoFile(video: VideoData): string {
  return fileTree[video[0]][video[1]][video[2]];
}
